export const area_status_change = (status, area_key) => ({
	type: "area_status_change",
	status,
	area_key
});
export const area_items_set = (items_array, area_key) => {
	const items_object = {};
	items_array.forEach(function(item) {
		items_object[item._id] = Object.assign({}, item);
	});
	items_object.length = items_array.length;
	return {
		type: "area_items_set",
		items_object,
		area_key
	};
};
export const area_item_edit = (item, area_key) => ({
	type: "area_item_edit",
	item,
	area_key
});
export const area_item_add = (item, area_key) => ({
	type: "area_item_add",
	item,
	area_key
});
export const area_filter_add = (filter, area_key) => ({
	type: "area_filter_add",
	filter,
	area_key
});
export const area_filter_remove = (filter, area_key) => ({
	type: "area_filter_remove",
	filter,
	area_key
});



